"use cleint";
import React, { useState } from "react";
import { Switch } from "@mui/material";
import DashboardIcon from "@mui/icons-material/Dashboard";
import PeopleIcon from "@mui/icons-material/People";
import BusinessIcon from "@mui/icons-material/Business";
import TableViewIcon from "@mui/icons-material/TableView";
import SettingsIcon from "@mui/icons-material/Settings";

const roles = ["Admin", "Manager", "Viewer"];

const menus = [
  { name: "Dashboard", icon: DashboardIcon },
  { name: "Users", icon: PeopleIcon },
  { name: "Company", icon: BusinessIcon },
  { name: "Menu Profile", icon: TableViewIcon },
  { name: "Settings", icon: SettingsIcon },
];

export default function RolesPermissionsTable() {
  const [activeRole, setActiveRole] = useState("Admin");

  const [permissions, setPermissions] = useState({
    Admin: {
      Dashboard: true,
      Users: true,
      Company: true,
      "Menu Profile": true,
      Settings: true,
    },
    Manager: {
      Dashboard: true,
      Users: false,
      Company: true,
      "Menu Profile": true,
      Settings: false,
    },
    Viewer: {
      Dashboard: true,
      Users: false,
      Company: false,
      "Menu Profile": true,
      Settings: false,
    },
  });

  const toggle = (menuName) => {
    setPermissions((prev) => ({
      ...prev,
      [activeRole]: {
        ...prev[activeRole],
        [menuName]: !prev[activeRole][menuName],
      },
    }));
  };

  return (
    <div className="p-10 bg-white text-gray-900 w-full max-w-4xl mx-auto">
      {/* Role Selector - clean pills */}
      <div className="flex gap-2 mb-6">
        {roles.map((role) => (
          <button
            key={role}
            onClick={() => setActiveRole(role)}
            className={`
              px-4 py-1.5 rounded-full text-sm border transition
              ${
                activeRole === role
                  ? "bg-gray-900 text-white border-gray-900"
                  : "bg-white border-gray-300 text-gray-700 hover:bg-gray-100"
              }
            `}
          >
            {role}
          </button>
        ))}
      </div>

      {/* The EXACT Notion-like table */}
      <table className="w-full table-auto border-collapse">
        <thead>
          <tr className="text-xs text-gray-500">
            <th className="text-left py-2 font-medium">Menu</th>
            <th className="text-left py-2 font-medium">Permission</th>
          </tr>
        </thead>

        <tbody>
          {menus.map((menu, idx) => (
            <tr key={menu.name} className="align-middle">
              {/* Row separator  */}
              <td colSpan={2}>
                {idx !== 0 && <div className="border-t border-gray-200"></div>}
              </td>

              {/* Actual content row */}
              <tr className="hover:bg-gray-50">
                {/* First Column */}
                <td className="py-3 flex items-center gap-3">
                  <menu.icon className="text-gray-400" fontSize="small" />
                  <span className="text-gray-800 text-sm">{menu.name}</span>
                </td>

                {/* Second Column - Toggle */}
                <td className="py-3">
                  <Switch
                    checked={permissions[activeRole][menu.name]}
                    onChange={() => toggle(menu.name)}
                    size="small"
                    sx={{
                      "& .MuiSwitch-switchBase.Mui-checked": { color: "#111" },
                      "& .MuiSwitch-switchBase.Mui-checked + .MuiSwitch-track":
                        {
                          backgroundColor: "#111",
                        },
                    }}
                  />
                </td>
              </tr>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
